<template>
  <section class="team-list-container">
    <div class="table-container">
      <table class="team-list" v-if="teamsForDisplay">
        <thead>
          <tr>
            <th>Member 1</th>
            <th>Member 2</th>
            <th>Member 3</th>
            <th></th>
            <!-- <th></th> -->
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="team in teamsForDisplay"
            :key="team.id"
            :class="{ 'is-match': team.isMatch }"
          >
            <td>{{ team.member1 }}</td>
            <td>{{ team.member2 }}</td>
            <td>{{ team.member3 }}</td>
            <!-- <td>{{ team._id }}</td> -->
            <td class="remove-team" @click="removeTeam(team._id)">X</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="list-footer">
      <div class="students-load">
        <h3>enter students as a CSV text</h3>
        <form @submit.prevent.stop="" class="students-load-form">
          <input class="form-el" type="text" v-model="students" />
          <!-- <p>{{ students }}</p> -->
          <button class="btn" @click="changeStudents">Load Students</button>
        </form>
      </div>
      <div class="flex">
        <select v-model="member1" class="form-el">
          <option
            v-for="(student, idx) in soloStudents"
            :key="idx"
            :value="student"
          >
            {{ student }}
          </option>
        </select>
        <button class="btn" @click="addTeam">Add Student</button>
      </div>
      <export-sheet />
    </div>
  </section>
</template>


<script>
import exportSheet from "../cmps/export-sheet";
import { teamService } from "../services/team.service.js";
export default {
  name: "team-list",
  data() {
    return {
      soloStudents: null,
      students: null,
      member1: "",
    };
  },
  created() {
    this.$store.dispatch("getTeams");
    this.loadSolo();
    // this.soloStudents = await this.soloStudentsForDisplay();
  },
  computed: {
    teamsForDisplay() {
      this.loadSolo();
      return this.$store.getters.teamsForDisplay;
    },
  },
  methods: {
    async loadSolo() {
      this.soloStudents = await this.soloStudentsForDisplay();
    },
    async soloStudentsForDisplay() {
      const students = await teamService.getStudents();
      const teams = this.teamsForDisplay;
      return students.filter((student) => {
        return teams.every(({ member1, member2, member3 }) => {
          return (
            student !== member1 && student !== member2 && student !== member3
          );
        });
      });
    },
    addTeam() {
      if (!this.member1) return;
      this.$store.dispatch("addTeam", { member1: this.member1 });
      this.member1 = "";
      this.loadSolo();
    },
    removeTeam(teamId) {
      this.$store.dispatch("removeTeam", teamId);
    },
    changeStudents() {
      if (this.students) this.$store.dispatch("changeStudents", this.students);
      else {
        console.log("No students found");
        this.$store.commit("sendMsg", {
          txt: "You must enter students to the list, CSV only",
          type: "warning",
        });
      }
    },
  },
  components: {
    exportSheet,
  },
};
</script>

<style lang="scss" scoped>
.team-list-container {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  .students-load {
    margin-bottom: 30px;
    .students-load-form {
      display: flex;
    }
  }

  .table-container {
    max-height: 350px;
    max-width: 100%;
    width: fit-content;
    overflow-x: overlay;
  }

  table {
    margin: auto;
    border-collapse: collapse;
    min-width: 340px;
    td,
    th {
      text-align: start;
      padding: 6px;
    }
    th {
      background-color: #d8d8d8;
    }

    td.remove-team {
      padding: 8px;
      font-weight: 600;
      text-align: center;
      cursor: pointer;
    }

    tr:not(:last-child) {
      border-bottom: 1px solid #e9e9e9;
    }
    tr.is-match {
      background-color: rgba(255, 217, 47, 0.178);
    }
  }
}
select {
  height: 44px;
  padding: 1px 2px;
}
@media (max-width: 400px) {
  .team-list-container {
    padding-top: 5px;
    .table-container {
      max-height: 330px;
    }
    table {
      min-width: 96vw;
    }
    .students-load {
      margin-bottom: 8px;
      .form-el {
        margin-bottom: 8px;
      }
    }
  }
}
</style>